@font-face {
  font-family: "Roboto-Light";
  src: url("../fonts/Roboto-Light.eot?#iefix") format("eot"), url("../fonts/Roboto-Light.ttf") format("truetype"), url("../fonts/Roboto-Light.woff") format("woff"), url("../fonts/Roboto-Light.woff2") format("woff2"); }

@font-face {
  font-family: "Roboto-Bold";
  src: url("../fonts/Roboto-Bold.eot?#iefix") format("eot"), url("../fonts/Roboto-Bold.ttf") format("truetype"), url("../fonts/Roboto-Bold.woff") format("woff"), url("../fonts/Roboto-Bold.woff2") format("woff2"); }

html, body {
  font-family: "Roboto-Light", sans-serif;
  width: 100%;
  height: 100%;
  color: #046541;
  margin: 0;
  background: #fff; }

#app, #main-app {
  width: 100%;
  height: 100%;
  position: relative; }

.main-layout {
  width: 100%;
  height: 100%; }

#header {
  width: 100%;
  height: 35%;
  display: inline-block;
  background: #046541 url("../images/background.jpg") no-repeat left top/cover;
  color: #fff;
  padding-top: 5%; }
  #header .title {
    text-decoration: underline;
    font-size: 3em;
    color: #fff; }
    @media (max-width: 767px) {
      #header .title {
        width: 100%;
        text-align: center;
        font-size: 1.5em; } }
  #header .you-know-that {
    font-family: 'Roboto-Bold', sans-serif;
    color: #fff;
    font-size: 1em;
    margin-top: 7.6%; }
    @media (max-width: 767px) {
      #header .you-know-that {
        margin-top: 19%; } }
  @media (max-width: 767px) {
    #header {
      padding-top: 23%; } }

.container {
  display: inline-block;
  width: 100%;
  height: 65%;
  background: #fff;
  color: #046541; }
  .container .you-know-that {
    font-family: 'Roboto-Bold', sans-serif; }

#header,
.container {
  box-sizing: border-box;
  position: relative;
  padding-left: 5.2%;
  padding-right: 5.2%; }

h2 {
  font-family: "Roboto-Light", sans-serif;
  text-decoration: underline; }

.forget-font-face {
  position: absolute;
  right: 20rem;
  top: 3em;
  text-decoration: line-through;
  font-size: 2rem; }
  @media (max-width: 767px) {
    .forget-font-face {
      right: 0;
      left: 0;
      top: 6em;
      text-decoration: line-through;
      font-size: 1.3rem;
      width: 100%;
      text-align: center; } }

.github-repo {
  background: transparent url("../images/github-mark.png") no-repeat left top/contain;
  position: absolute;
  right: 2em;
  cursor: pointer;
  text-decoration: none;
  width: 2em;
  top: 2em;
  padding-top: 10%; }



